import React, { Component } from 'react';
import './App.css';
import StickerSheet from './components/StickerSheet.js';

class App extends Component {
  render() {
    return (

      <div className="App">
        <div className="graph-paper">
          <h2>Monday</h2>
          <StickerSheet />
        </div>
      </div>
    );
  }
}

export default App;



